<div class="hello"><div class="ui container" v-if="uid"><form class="ui form error warning success" id="main-form"><h2 class="ui header">請填表</h2><div class="sub header">為了讓人真的能找到夥伴<br>有標記紅色星號<i class="red star"></i>的欄位為必填</div><h4 class="ui dividing header">關於場地</h4><div class="field"><label class="required">地名</label><input type="text" v-model="root.name" placeholder="場地名稱"></div><div class="field"><label class="required">概略地址</label><input v-model.trim="root.address" placeholder="地址愈詳細，別人愈好認識你的所在"><h4 class="ui header">手動拖拉地圖讓標記移到位置附近<div class="sub header">經緯座標： {{root.latlngColumn}}</div></h4><div id="map" style="height: 300px;"> // Add the map container</div><div class="ui error message" v-show="!root.latlngColumn"><div class="header">無法定位</div><p>如果地場有不只一個位置，請填寫一個就好，其他可寫進介紹中。<br>若持續無法定位，請<a href="https://www.google.com.tw/chrome/browser/desktop/" target="_blank">使用Chrome瀏覽器</a>再試一次。</p></div><div class="ui warning message" v-show="usedAddr(root)"><div class="header">地址重疊</div><p>有別人使用了這個地址。您可以加上村里或是路段，來使定位更精準</p></div></div><div class="ui divider"></div><div class="field"><label>網址</label><div class="two fields"><div class="field"> <input v-model="root.site" placeholder="場地網址"><span v-show="root.site"><a :href="root.site" target="_blank"><img :src="'http://www.google.com/s2/favicons?domain=' + root.site" title="場地網址" alt="場地網址">測試連結</a></span></div></div></div><div class="two fields"><div class="field"><label class="required">有效聯絡方式</label><input type="text" v-model="root.connect_me" placeholder="可公開的方式，如e-mail。臉書私訊不可"></div><div class="ui divider"></div><div class="field"><label class="required">介紹</label><textarea id="note" v-model="root.note" rows="5" cols="30" placeholder="介紹很重要，請寫20字以上，謝謝"></textarea></div></div></form><div class="ui divider"></div><a class="ui large blue button" @click="updatePlace"><span>升起旗幟!!</span></a></div></div>